import React from "react";
import { ButtonGroup, Button, Modal, Form } from 'react-bootstrap';
import moment from 'moment';
import config from '../config'

import { GetCommonData, InsertData, UpdateData, DeleteData, GetDashboardUrl} from "../store/actions/CommonAction";

import { If, Then } from 'react-if';

import { connect } from "react-redux";

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import { fnDatatableCol, fnCleanData, GetJsonToArray, getUrlParameter } from '../utility/utility.jsx';
// reactstrap components
import {
  Card,
  CardHeader,
  CardBody,
  CardTitle,
  Row,
  Col
} from "reactstrap";
import { getuser } from "utility/utility";

class QuickSightVisualizationTerm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
        PageTitle: "QuickSight Visualization Term",
    };
    this.ecode = null;
}


  componentDidMount() {
    console.log("getuser", getuser());
    let ecode = getuser().EmployeeId;
    this.ecode = ecode;
    this.refs.iframeref.setAttribute('onLoad', this.handleOpen());
  }

  handleOpen() {
    
    let iframe = document.getElementById('iframeid');
    iframe.style.height = "1200px";
    iframe.style.width = "100%";

    GetDashboardUrl({
        "userid": this.ecode,
    }, (result) => {
        console.log('-----result', result);
        if(result?.data?.QuicksightUrl){
          iframe.src = result.data.QuicksightUrl;
        }
    })
  }

  render() {
    const { PageTitle } = this.state;
    return (
      <>
        <div className="content">
          <ToastContainer />
          <Row>
            <Col md="12">
              <Card>
                <CardHeader>
                  <Row>
                    <Col md={10}>
                      <CardTitle tag="h3">{PageTitle}</CardTitle>
                    </Col>
                  </Row>
                </CardHeader>
                <CardBody>
                  <iframe id="iframeid" ref="iframeref" src="" {...this.props}></iframe>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </div>
      </>
    );
  }
}


function mapStateToProps(state) {
  return {
    CommonData: state.CommonData
  };
}

export default connect(
  mapStateToProps,
  {
    GetCommonData,
    InsertData,
    UpdateData,
    DeleteData
  }
)(QuickSightVisualizationTerm);